.alertContainer {
	position: absolute;
	bottom: 0;
	margin: var(--size-400);
	padding: var(--size-400) var(--size-100) var(--size-400) var(--size-300);
	color: var(--text-clr);
	background-color: var(--bg-clr);
	border-radius: var(--size-200);
	box-shadow: var(--box-shadow);
	z-index: var(--index-higher);
}
.alertContainer::before{
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	border-radius: var(--size-200);
	background: linear-gradient(to right, var(--text-clr), var(--text-clr));
	background-repeat: no-repeat;
	background-position: 0% bottom;
	animation: alertBottomStrike 3s linear 1 forwards;
}
 .cancleAlert{
	 width: var(--size-500);
	 height: var(--size-500);
	 z-index: var(--index-higher);
 }
@keyframes alertBottomStrike {
	0% {
		background-size: 100% 4px;
	}
   100%{
      background-size: 0% 4px;
   }
}
